li.list {
    list-style-type: square;
}

.card-action {
    text-align: center
}
/************************************************/
.pricing-table {
    margin-top: 50px;
    border: 4px solid #ff5253;
}

.pricing-table .pricing-header {
    position: relative;
    padding: 20px 0;
    text-align: center;
}

.pricing-table .pricing-header i {
    position: absolute;
    top: 0;
    left: 50%;
    width: 80px;
    padding-left: 5px;
    margin-left: -40px;
    font-size: 35px;
    background-color: #ffffff;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    color: #ff5253;
}


