#nav-resume {
    background-color: #ff5253;
}
#nav-resume li a {
    color: white;
}


nav ul span {
    -webkit-transition: background-color .3s;
    -o-transition: background-color .3s;
    transition: background-color .3s;
    font-size: 1rem;
    color: #fff;
    display: block;
    padding: 0 15px;
    cursor: pointer;
}


