.body-page {
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
}

main {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  font-family: supermarket;
}

.text-redpink {
  color: #ff5253;
}

.radius-5 {
  border-radius: 5px
}

.radius-10 {
  border-radius: 10px;
}

.font-18 {
  font-size: 18px;
}

.font-25 {
  font-size: 25px;
}

.mt-10 {
  margin-top: 10px;
}

.btn-contact-now , .btn-contact-now:hover {
  background: #fff;
  color: #ff5253;
  border: 1px solid #ff5253;
  border-radius: 20px;
  font-size: large;
  height: 50px;
  margin: 0 5px;
}

.btn-portfolio-now , .btn-portfolio-now:hover {
  background: #ff5253;
  color: #fff;
  border: 1px solid #ff5253;
  border-radius: 20px;
  font-size: large;
  height: 50px;
  margin: 0 5px;
}

.z-depth-0{
  -webkit-box-shadow: 0 0 0 0 rgba(0,0,0,0.14), 0 0 0 0 rgba(0,0,0,0.12), 0 0 0 0 rgba(0,0,0,0.2);
  box-shadow: 0 0 0 0 rgba(0,0,0,0.14), 0 0 0 0 rgba(0,0,0,0.12), 0 0 0 0 rgba(0,0,0,0.2);
}


